import React, { useEffect } from "react"
import { useStoreState, useStoreActions } from 'easy-peasy'
// import { makeStyles } from '@material-ui/core/styles'
import ViewListIcon from '@material-ui/icons/ViewList'
import Alert from '@material-ui/lab/Alert'

import Tables from './../../components/material/Tables'

import { IfElse } from '../../components/If'

import { Container, Group, Background } from './style'


const headCells = [
  { id: 'name_agency', numeric: false, disablePadding: false, label: 'Razo Social' },
  { id: 'agency_fancy', numeric: false, disablePadding: false, label: 'Nome Fantasia' },
  { id: 'cnpj', numeric: false, disablePadding: false, label: 'CNPJ' },
  { id: 'aberje_associate', numeric: false, disablePadding: false, label: 'Assoc.' },
  { id: 'cep', numeric: false, disablePadding: false, label: 'CEP' },
  { id: 'address', numeric: false, disablePadding: false, label: 'Endereo' },
  { id: 'address_complement', numeric: false, disablePadding: false, label: 'Compl.' },
  { id: 'neighborhood', numeric: false, disablePadding: false, label: 'Bairro' },
  { id: 'city', numeric: false, disablePadding: false, label: 'Cidade' },
  { id: 'qtd_projects', numeric: true, disablePadding: false, label: 'Projetos' },
  { id: 'clients', numeric: false, disablePadding: false, label: 'Clientes' },
  { id: 'name', numeric: false, disablePadding: false, label: 'Resp.' },
  { id: 'email', numeric: false, disablePadding: false, label: 'Email' },
  { id: 'phone', numeric: false, disablePadding: false, label: 'Cel' },
  { id: 'observation', numeric: false, disablePadding: false, label: 'Obs.' },
];

// const useStyles = makeStyles(theme => ({
//   root: {
//     width: '50%',
//     '& > * + *': {
//       marginTop: theme.spacing(3),
//     },
//   },
// }));

const AgenciesList = () => {
  const agencies = useStoreState(state => state.agency.agencies)
  const getAllAgencies = useStoreActions(actions => actions.agency.getAllAgencyUsers)

  useEffect(() => {
    getAllAgencies()
  }, [getAllAgencies])

  const clearList = agencies
    .map(agency => ({
      id: agency.agency_id,
      name_agency: agency.agency_name,
      agency_fancy: agency.agency_fancy,
      cnpj: agency.cnpj,
      name: agency.name,
      clients: agency.clients,
      email: agency.email,
      phone: agency.phone,
      observation: agency.observation,
      qtd_projects: agency.qtd_projects,
      aberje_associate: agency.aberje_associate === true ? 'Sim' : 'No',
      address: agency.address,
      cep: agency.cep,
      neighborhood: agency.neighborhood,
      address_complement: agency.address_complement,
      city: agency.city,
    }))



  const filterClearList = clearList.filter((agency) => {
    return agency.id !== undefined
  })

  return (
    <Background className="container clearfix et_menu_container">
      <Container>
        <Group>
          <IfElse
            condition={
              typeof agencies !== 'undefined' && agencies.length > 0
            }
            True={

              <Tables
                title="Agncias"
                headCells={headCells}
                list={filterClearList}
                actions={[
                  {
                    action: '/listagem/projetos/',
                    btn: <ViewListIcon />,
                    type: 'link', // link or btn
                    tooltip: 'Ver lista de projetos postados'
                  },

                ]}
              />

            }
            False={
              <Alert severity="warning">No h projetos cadastrados</Alert>
            }
          />
        </Group>
      </Container>
    </Background>
  )
}

export default AgenciesList
