import React, { useState, useEffect } from 'react'
import { cnpj } from 'cpf-cnpj-validator';
import cep from 'cep-promise';
import { useForm } from 'react-hook-form'
import { useStoreActions } from 'easy-peasy'

import Typography from '@material-ui/core/Typography'
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete'


import Flexbox from '../../components/Flexbox'
import Button from '../../components/material/Button'
import Snackbar from '@material-ui/core/Snackbar';
import Alert from '@material-ui/lab/Alert';

import ChipOptions from '../../components/material/ChipOptions'
import Switch from '../../components/material/Switch'
import Grid from '@material-ui/core/Grid'



import {
  qtd_projects
} from './dicioFields'
import { Form, Background, WrapButton, WrapObs, TitleObs, AssociateLegalText } from './styles'
import { cepMask, cnpjMask, phoneMask } from '../../utils/masks';
import history from '../../history';

const Agency = () => {
  const { register, handleSubmit, errors, setValue, setError, clearErrors, getValues } = useForm()

  const registerAgency = useStoreActions(actions => actions.register.registerAgency)
  const [snackbarState, setSnackbarState] = useState({ active: false, severity: null, message: null })
  const [aberjeAssociate, setAberjeAssociate] = useState(false)
  const [numCols] = useState(4)

  const onSubmit = async (agencyData) => {
    const formatted = {
      ...agencyData,
      qtd_projects: agencyData.qtdProjects,
      clients: agencyData?.clients.split(","),
      type: 'agency',
    }
    const response = await registerAgency(formatted);
    if (response.data && response.status === 200) {
      setSnackbarState({
        active: true,
        severity: 'success',
        message: 'Inscrio Realizada com sucesso! Em breve receber um e-mail da organizao',
      })
      setTimeout(() => {
        return history.push(`/dashboard/agencia`)
      }, 4000);
      return;
    }

    if (response.errors) {
      const errorMessage = Object.values(response.errors)
      setSnackbarState({
        active: true,
        severity: 'error',
        message: errorMessage,
      })
      return false;
    }
  }


  const getPostalData = (e) => {
    const value = e.target.value;
    if (value.length < 9) return;

    cep(value)
      .then((data) => {
        setValue("city", data.city);
        setValue("neighborhood", data.neighborhood);
        setValue("address", data.street);
        clearErrors('cep')
      }).catch((err) => {
        console.log(err);
        setError('cep',{
          type: "manual",
          message: "CEP no encontrado",
        })
      });
  };

  const handleCnpjValidator = ({ target }) => {
    const cnpjIsValid = cnpj.isValid(target.value);
    if (!cnpjIsValid) return setError('cnpj', {
      type: "manual",
      message: "CNPJ invlido."
    });
    clearErrors('cnpj');
  }


  // const handleRadio = (field, selectedOption) => setValue(field, (selectedOption.toLowerCase() === 'true'))

  useEffect(() => {
    register({ name: 'qtdProjects' });
    if (typeof localStorage.user_type === 'undefined') return setSnackbarState({ active: false })
  }, [register]);

  // TODO: req hasNoRegister p/ validar se o usurio tem algum registro como profissional ou empresa. Se sim, redireciona para o dashboard, se no, mantm na pgina.
  return (
    <Background>
      <Flexbox justify="center">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Typography variant="h4" component="h2">Ficha de inscrio</Typography>

          <WrapObs>
            <TitleObs>Nesta pgina, voc preencher a ficha de inscrio. Essa ficha, oficializa a sua participao no <b>Prmio Aberje 2020.</b></TitleObs>
            <TitleObs>Atravs deste preenchimento que receber boleto bancrio e recibo, esses documentos sero enviados via e-mail.</TitleObs>
            <TitleObs>Neste momento voc no incluir dados do projeto, somente na ficha de identificao, que  o prximo passo.</TitleObs>
          </WrapObs>

          <Grid item xs={numCols}>
            <Switch
              name="aberje_associate"
              label="Associado Aberje"
              register={register}
              onChange={(e) => setAberjeAssociate(e.target.checked)}
            />
          </Grid>

          <Grid container spacing={2}>
            <Grid item xs={6}>
              <TextField
                name="agency_name"
                fullWidth
                error={errors.agency_name && errors.agency_name.message}
                helperText={errors.agency_name && errors.agency_name.message}
                inputRef={register({
                  required: 'Esse campo  obrigatrio'
                })}
                label="Razo Social"
                variant="filled"
                placeholder="Insira a razo social"

              />
            </Grid>


            <Grid item xs={6}>
              <TextField
                name="agency_fancy"
                fullWidth
                error={errors.agency_fancy && errors.agency_fancy.message}
                helperText={errors.agency_fancy && errors.agency_fancy.message}
                inputRef={register({
                  required: 'Esse campo  obrigatrio'
                })}
                label="Nome Fantasia"
                variant="filled"
                placeholder="Insira o nome fantasia"

              />
            </Grid>
          </Grid>
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <TextField
                name="cnpj"
                fullWidth
                error={errors.cnpj && errors.cnpj.message}
                helperText={errors.cnpj && errors.cnpj.message}
                inputRef={register({
                  required: 'Esse campo  obrigatrio'
                })}
                onBlur={handleCnpjValidator}
                onInput={({ target }) => target.value = cnpjMask(target.value)}
                label="CNPJ"
                variant="filled"
                placeholder="Insira o CNPJ da empresa"

              />
            </Grid>


            <Grid item xs={6}>
              <TextField
                name="agency_tel"
                fullWidth
                error={errors.agency_tel && errors.agency_tel.message}
                helperText={errors.agency_tel && errors.agency_tel.message}
                inputRef={register({
                  required: 'Esse campo  obrigatrio'
                })}
                onInput={({ target }) => target.value = phoneMask(target.value)}
                label="Telefone"
                variant="filled"
                placeholder="Insira o Telefone da empresa"

              />
            </Grid>
          </Grid>

          <Grid container spacing={2}>
            <Grid item xs={4}>
              <TextField
                name="cep"
                onBlur={getPostalData}
                onInput={({ target }) => target.value = cepMask(target.value)}
                fullWidth
                error={errors.cep && errors.cep.message}
                helperText={errors.cep && errors.cep.message}
                inputRef={register({
                  required: 'Esse campo  obrigatrio'
                })}
                label="CEP"
                variant="filled"
                placeholder="Insira o CEP da empresa"

              />
            </Grid>
            <Grid item xs={4}>
              <TextField
                name="city"
                fullWidth
                error={errors.city && errors.city.message}
                helperText={errors.city && errors.city.message}
                inputRef={register({
                  required: 'Esse campo  obrigatrio'
                })}
                InputLabelProps={{
                  shrink: Boolean(getValues().city)
                }}
                label="Cidade"
                variant="filled"
                placeholder="Insira a cidade que a da empresa reside"

              />
            </Grid>


            <Grid item xs={4}>
              <TextField
                name="neighborhood"
                fullWidth
                error={errors.city && errors.city.message}
                helperText={errors.city && errors.city.message}
                inputRef={register({
                  required: 'Esse campo  obrigatrio'
                })}
                InputLabelProps={{
                  shrink: Boolean(getValues().neighborhood)
                }}
                label="Bairro"
                variant="filled"
                placeholder="Insira o bairro que a da empresa reside"

              />
            </Grid>
          </Grid>
          <Grid container spacing={3}>
            <Grid item xs={6}>
              <TextField
                name="address"
                fullWidth
                error={errors.address && errors.address.message}
                helperText={errors.address && errors.address.message}
                inputRef={register({
                  required: 'Esse campo  obrigatrio'
                })}
                InputLabelProps={{
                  shrink: Boolean(getValues().address)
                }}
                label="Endereo"
                variant="filled"
                placeholder="Insira o endereo da empresa"

              />
            </Grid>
            <Grid item xs={2}>
              <TextField
                name="address_number"
                fullWidth
                error={errors.address_number && errors.address_number.message}
                helperText={errors.address_number && errors.address_number.message}
                inputRef={register({
                  required: 'Esse campo  obrigatrio'
                })}
                type="number"
                label="Nmero"
                variant="filled"
                placeholder="Nmero"
              />
            </Grid>

            <Grid item xs={4}>
              <TextField
                name="address_complement"
                fullWidth
                error={errors.address_complement && errors.address_complement.message}
                helperText={errors.address_complement && errors.address_complement.message}
                label="Complemento"
                variant="filled"
                placeholder="Insira o completo"
                inputRef={register({
                  required: 'Esse campo  obrigatrio'
                })}
              />
            </Grid>
          </Grid>
          <Typography variant="h6" component="h6">Informaes do Projeto</Typography>

          <Grid container spacing={2}>

            <Grid item xs={4}>
              <Autocomplete
                options={qtd_projects}
                renderInput={params => (
                  <TextField
                    {...params}
                    name="qtdProjects"
                    inputRef={register({
                      required: 'Esse campo  obrigatrio'
                    })}
                    color="secondary"
                    label="Quantidade de Projetos"
                    variant="filled"
                    placeholder="Selecione a quantidade"
                    error={errors.qtdProjects && errors.qtdProjects.message}
                    helperText={errors.qtdProjects && errors.qtdProjects.message}
                  />
                )}
              />

            </Grid>
            <Grid item xs={4}>
              <ChipOptions
                name="clients"
                label="Insira os nomes dos clientes"
                error={errors.clients && errors.clients.message}
                register={register({
                  required: 'Esse campo  obrigatrio',
                  minLength: {
                    value: 2,
                    message: 'Insira pelo menos um cliente'
                  }
                })}
              />
            </Grid>
            <Grid item xs={4}>
              <TextField
                id="filled-multiline-static"
                label="Observao"
                multiline
                rows="5"
                error={errors.observation && errors.observation.message}
                fullWidth
                name="observation"
                variant="filled"
                inputRef={register}
              />
            </Grid>
          </Grid>
          <WrapButton>
            <AssociateLegalText>
              Declaro que estou ciente de que todos os projetos inscritos esto passveis de anlise por parte da equipe do Prmio Aberje 2020
              e que pode ser excludo se estiver em desacordo com o&nbsp;
              <a href="http://aberje.com.br/premio/images/pdf/regulamento/regulamento_premio_2020.pdf" rel="noopener noreferrer" target="_blank">
                regulamento
              </a>.
              <Switch
                name="agree_terms"
                label="Estou de acordo"
                register={register}
                required
                error={errors.agree_terms && errors.agree_terms.message}
              />
            </AssociateLegalText>
            <Button variant="contained" type="submit">
              Continuar
            </Button>
          </WrapButton>
          <Snackbar
            open={Boolean(snackbarState.active)}
            anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            autoHideDuration={6000}
            onClose={() => setSnackbarState({ active: false })}
          >
            <Alert elevation={6} variant="filled" onClose={() => setSnackbarState({ active: false })} severity={snackbarState.severity}>
              {snackbarState.message}
            </Alert>
          </Snackbar>

        </Form>
      </Flexbox>
    </Background >
  )
}

export default Agency