import React, { useEffect } from "react"
import { useStoreState, useStoreActions } from 'easy-peasy'
// import { makeStyles } from '@material-ui/core/styles'
import ViewListIcon from '@material-ui/icons/ViewList'
import Alert from '@material-ui/lab/Alert'

import Tables from './../../components/material/Tables'

import { IfElse } from '../../components/If'

import { Container, Group, Background } from './style'

const headCells = [
  { id: 'name_enterprise', numeric: false, disablePadding: false, label: 'Razo Social' },
  { id: 'enterprise_fancy', numeric: false, disablePadding: false, label: 'Nome Fantasia' },
  { id: 'cnpj', numeric: false, disablePadding: false, label: 'CNPJ' },
  { id: 'aberje_associate', numeric: true, disablePadding: true, label: 'Associado' },
  { id: 'cep', numeric: false, disablePadding: false, label: 'CEP' },
  { id: 'address', numeric: false, disablePadding: false, label: 'Endereo' },
  { id: 'address_complement', numeric: false, disablePadding: false, label: 'Compl.' },
  { id: 'neighborhood', numeric: false, disablePadding: false, label: 'Bairro' },
  { id: 'city', numeric: false, disablePadding: false, label: 'Cidade' },
  { id: 'qtd_projects', numeric: true, disablePadding: false, label: 'Projetos' },
  { id: 'name', numeric: false, disablePadding: false, label: 'Responsvel' },
  { id: 'email', numeric: false, disablePadding: false, label: 'E-mail' },
  { id: 'phone', numeric: false, disablePadding: false, label: 'Cel' },
  { id: 'observation', numeric: false, disablePadding: false, label: 'Obs.' },
];


// const useStyles = makeStyles(theme => ({
//   root: {
//     width: '50%',
//     '& > * + *': {
//       marginTop: theme.spacing(3),
//     },
//   },
// }));

const EnterprisesList = () => {
  const enterprises = useStoreState(state => state.enterprise.enterprises)
  const getAllEnterprises = useStoreActions(actions => actions.enterprise.getAllEnterpriseUsers)

  useEffect(() => {
    getAllEnterprises()
  }, [getAllEnterprises])


  const clearList = enterprises
    .map((enterprise) => ({
      id: enterprise.enterprise_id,
      name_enterprise: enterprise.enterprise_name,
      enterprise_fancy: enterprise.enterprise_fancy,
      cnpj: enterprise.cnpj,
      aberje_associate: enterprise.aberje_associate === true ? 'Sim' : 'No',
      name: enterprise.name,
      email: enterprise.email,
      phone: enterprise.phone,
      observation: enterprise.observation,
      qtd_projects: enterprise.qtd_projects,
      address: enterprise.address,
      cep: enterprise.cep,
      neighborhood: enterprise.neighborhood,
      address_complement: enterprise.address_complement,
      city: enterprise.city
    }))


  const filterClearList = clearList.filter((enterprise, indice) => {
    return enterprise.id !== undefined
  })

  return (
    <Background className="container clearfix et_menu_container">
      <Container>
        <Group>
          <IfElse
            condition={
              typeof enterprises !== 'undefined' && enterprises.length > 0
            }
            True={
              <Tables
                title="Empresas"
                headCells={headCells}
                list={filterClearList}
                actions={[
                  {
                    action: '/listagem/projetos/',
                    btn: <ViewListIcon />,
                    type: 'link', // link or btn
                    tooltip: 'Ver lista de projetos postados'
                  }
                ]}
              />
            }
            False={
              <Alert severity="warning">No h empresas cadastradas</Alert>
            }
          />
        </Group>
      </Container>
    </Background>
  )
}

export default EnterprisesList
